.leaf {
	position: relative;
	padding: 2px;
	margin: 5px;
	border: 1px solid #000;
	-webkit-box-shadow: 2px 2px 5px rgba(0,0,0, .7);
	        box-shadow: 2px 2px 5px rgba(0,0,0, .7);
	margin: 5px 25px;
	cursor: pointer;
	min-width: 155px;
	min-height: 155px;
	border-radius: .25rem;
}

.leaf:hover {
	-webkit-transform: scale(1.05);
	    -ms-transform: scale(1.05);
	        transform: scale(1.05);
	-webkit-box-shadow: 10px 10px 10px rgba(0,0,0,.7);
	        box-shadow: 10px 10px 10px rgba(0,0,0,.7);
}

.leaf img {
	width: 150px;
	height: 150px;
}

.hard-mode .leaf {
	-webkit-filter: grayscale(70%);
	        filter: grayscale(70%);
}

@media screen and (max-width: 576px){
	.leaf {
		min-width: 50px;
    min-height: 50px;
    margin: 22px 15px;
	}

	.leaf img {
		width: 50px;
		height: 50px;
	}

	.leaf:hover {
		-webkit-box-shadow: 2px 2px 10px rgba(0,0,0,.7);
		        box-shadow: 2px 2px 10px rgba(0,0,0,.7);
	}
}



