.App-header {
	background-color: rgba(0, 194, 0, 1);
	color: white;
	-webkit-box-shadow: 0 0 25px rgba(0,0,0,.7);
	        box-shadow: 0 0 25px rgba(0,0,0,.7);
	opacity: .8;
}

.App-header a {
	color: #fff;
	text-decoration: none;
	line-height: 2rem;
}

.navbar {
	position: fixed;
	width: 100%;
	top: 0;
	z-index: 1;
}

.navbar-main {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	width: 100%;
}

.text-danger {
	-webkit-animation: text_blink 1.5s;
	        animation: text_blink 1.5s;
}

.text-winner {
	-webkit-transition: all 500ms;
	-o-transition: all 500ms;
	transition: all 500ms;
	-webkit-animation: rainbow 3s ease;
	        animation: rainbow 3s ease;
}

.navbar span {
	position: relative;
}

.navbar span.increase-score {
	-webkit-animation: add_to_score 3s .625;
	        animation: add_to_score 3s .625;
}

.score {
	font-size: 1.5rem;
	font-weight: 500;
	line-height: 1.2;
	margin-bottom: .5rem;
}

@-webkit-keyframes text_blink {
	from { opacity: 0 }
	to { opacity: 1 }
}

@keyframes text_blink {
	from { opacity: 0 }
	to { opacity: 1 }
}

@-webkit-keyframes add_to_score {
	0% {
		top: 0;
		left: 0;
	}

	30% {
		top: 20px;
		left: 20px;
		font-size: 1rem;
	}

	80% {
		top: -40px;
		left: -50px;
		font-size: 5px;
	}
}

@keyframes add_to_score {
	0% {
		top: 0;
		left: 0;
	}

	30% {
		top: 20px;
		left: 20px;
		font-size: 1rem;
	}

	80% {
		top: -40px;
		left: -50px;
		font-size: 5px;
	}
}

@-webkit-keyframes rainbow {
	0%{color: red;}
	30%{color: blue;}
	40%{color: yellow;}
	50%{color: green;}
	60%{color: purple;}
	67%{color: white;}
}

@keyframes rainbow {
	0%{color: red;}
	30%{color: blue;}
	40%{color: yellow;}
	50%{color: green;}
	60%{color: purple;}
	67%{color: white;}
}

@media screen and (max-width: 768px) {
	.navbar {
		position: relative;
		margin-bottom: 10px;
	}

	.status {
		display: none;
	}
}

@media screen and (min-width: 577px) {
	.countdown-a {
		display: block;
	}

	.countdown-b {
		display: none;
	}
}

@media screen and (max-width: 576px) {
	.score {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-flow: column;
		flex-flow: column;
		font-size: .75rem;
		text-align: right;
	}

	.score-divider {
		display: none;
	}

	.countdown-a {
		display: none;
	}

	.countdown-b {
		display: block;
		width: 100%;
	}
}



